import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import { List, Divider, Paper } from "@material-ui/core";
import SubscriptionTable from "./SubscriptionTable";
import UpdateSubscription from "./UpdateSubscription";
import SubscriptionInfo from "./SubscriptionInfo";

const styles = theme => ({
  contentAreaInnerArea: {
    margin: theme.spacing(1),
    width: "auto",
    [theme.breakpoints.up("xs")]: {
      width: "90%",
      marginLeft: "auto",
      marginRight: "auto",
      marginTop: theme.spacing(4),
      marginBottom: theme.spacing(4)
    },
    [theme.breakpoints.up("sm")]: {
      marginTop: theme.spacing(6),
      marginBottom: theme.spacing(6),
      width: "70%",
      marginLeft: "auto",
      marginRight: "auto"
    },
    [theme.breakpoints.up("md")]: {
      marginTop: theme.spacing(6),
      marginBottom: theme.spacing(6),
      width: "50%",
      marginLeft: "auto",
      marginRight: "auto"
    }
  }
});

class Subscription extends PureComponent {
  state = {
    paperOpen: "SubscriptionTable",
    plans: []
  };

  componentDidMount() {
    this.fetchPlans();
  }

  fetchPlans = () => null;

  setPaperOpen = paperOpen => {
    this.setState({ paperOpen });
  };

  printContent = () => {
    const { transactions, userData, pushMessageToSnackbar } = this.props;
    const { paperOpen, plans } = this.state;
    switch (paperOpen) {
      case "SubscriptionTable": {
        return (
          <Paper>
            <List disablePadding>
              <SubscriptionInfo
                userData={userData}
                setPaperOpen={this.setPaperOpen}
              />
              <Divider />
              <SubscriptionTable transactions={transactions} />
            </List>
          </Paper>
        );
      }
      case "UpdateSubscription": {
        return (
          <UpdateSubscription
            setPaperOpen={this.setPaperOpen}
            plans={plans}
            pushMessageToSnackbar={pushMessageToSnackbar}
          />
        );
      }
      default: {
        throw new Error("No branch selected in switch statement.");
      }
    }
  };

  render() {
    return this.printContent();
  }
}

Subscription.propTypes = {
  userData: PropTypes.object,
  transactions: PropTypes.array,
  pushMessageToSnackbar: PropTypes.func
};

export default withStyles(styles, { withTheme: true })(Subscription);
