import React, { PureComponent, Fragment } from "react";
import PropTypes from "prop-types";
import ScheduledPostContent from "./ScheduledPostContent";
import AddScheduledPost from "./AddScheduledPost";

class SchedulePosts extends PureComponent {
  state = {
    addPostPaperOpen: false
  };

  openAddPostModal = () => {
    const { currentInstaId } = this.props;
    if (currentInstaId) {
      this.setState({ addPostPaperOpen: true });
    }
  };

  closeAddPostModal = () => {
    this.setState({ addPostPaperOpen: false });
  };

  onEdit = element => {
    this.setState({ addPostPaperOpen: true }, () => {
      this.editFunc(element);
    });
  };

  receiveEditFunctionFromChild = func => {
    this.editFunc = func;
  };

  render() {
    const { addPostPaperOpen, addPostModalOpen } = this.state;
    const {
      EmojiTextArea,
      ImageCroppr,
      Dropzone,
      DateTimePicker,
      pushMessageToSnackbar,
      currentInstaId,
      fetchScheduledPosts,
      hasFetchedScheduledPosts,
      hasFetchedInstaAccounts,
      scheduledPosts
    } = this.props;
    return (
      <Fragment>
        {addPostPaperOpen ? (
          <AddScheduledPost
            onClose={this.closeAddPostModal}
            open={addPostModalOpen}
            EmojiTextArea={EmojiTextArea}
            ImageCroppr={ImageCroppr}
            Dropzone={Dropzone}
            DateTimePicker={DateTimePicker}
            pushMessageToSnackbar={pushMessageToSnackbar}
            currentInstaId={currentInstaId}
            fetchScheduledPosts={fetchScheduledPosts}
            receiveEditFunctionFromChild={this.receiveEditFunctionFromChild}
          />
        ) : (
          <ScheduledPostContent
            openAddPostModal={this.openAddPostModal}
            currentInstaId={currentInstaId}
            hasFetchedInstaAccounts={hasFetchedInstaAccounts}
            scheduledPosts={scheduledPosts}
            hasFetchedScheduledPosts={hasFetchedScheduledPosts}
            fetchScheduledPosts={fetchScheduledPosts}
            pushMessageToSnackbar={pushMessageToSnackbar}
            onEdit={this.onEdit}
          />
        )}
      </Fragment>
    );
  }
}

SchedulePosts.propTypes = {
  currentInstaId: PropTypes.number,
  EmojiTextArea: PropTypes.oneOfType([PropTypes.func, PropTypes.element]),
  ImageCroppr: PropTypes.oneOfType([PropTypes.func, PropTypes.element]),
  Dropzone: PropTypes.oneOfType([PropTypes.func, PropTypes.element]),
  DateTimePicker: PropTypes.oneOfType([PropTypes.func, PropTypes.element]),
  fetchScheduledPosts: PropTypes.func,
  hasFetchedScheduledPosts: PropTypes.bool,
  hasFetchedInstaAccounts: PropTypes.bool,
  scheduledPosts: PropTypes.array,
  pushMessageToSnackbar: PropTypes.func
};

export default SchedulePosts;
