import React, { PureComponent, Fragment } from "react";
import PropTypes from "prop-types";
import SelectPlan from "./SelectPlan";
import SelectOption from "./SelectOption";
import ConfirmSubscriptionCancelation from "./ConfirmSubscriptionCancelation";
import ConfirmPlanSwitch from "./ConfirmPlanSwitch";

class UpdateSubscription extends PureComponent {
  state = {
    activeStep: "SelectOption",
    selectedPlan: null
  };

  selectPlan = selectedPlan => {
    this.setState({ selectedPlan });
  };

  setActiveStep = activeStep => {
    this.setState({ activeStep });
  };

  printContent = () => {
    const {
      plans,
      userData,
      setPaperOpen,
      fetchTransactions,
      refreshUserData,
      pushMessageToSnackbar,
      refreshAccountSlots
    } = this.props;
    const { activeStep, selectedPlan } = this.state;
    switch (activeStep) {
      case "SelectOption":
        return (
          <SelectOption
            userData={userData}
            onClose={() => {
              setPaperOpen("SubscriptionTable");
            }}
            setActiveStep={this.setActiveStep}
          />
        );
      case "SwitchPlan": {
        return (
          <SelectPlan
            plans={plans}
            selectPlan={this.selectPlan}
            selectedPlan={selectedPlan}
            setActiveStep={this.setActiveStep}
            userData={userData}
          />
        );
      }
      case "ConfirmSubscriptionCancelation": {
        return (
          <ConfirmSubscriptionCancelation
            setActiveStep={this.setActiveStep}
            onSuccess={() => {
              setPaperOpen("SubscriptionTable");
            }}
            fetchTransactions={fetchTransactions}
            refreshUserData={refreshUserData}
            pushMessageToSnackbar={pushMessageToSnackbar}
            refreshAccountSlots={refreshAccountSlots}
            userData={userData}
          />
        );
      }
      case "ConfirmPlanSwitch": {
        return (
          <ConfirmPlanSwitch
            selectedPlan={selectedPlan}
            setActiveStep={this.setActiveStep}
            onSuccess={() => {
              setPaperOpen("SubscriptionTable");
            }}
            fetchTransactions={fetchTransactions}
            refreshUserData={refreshUserData}
            pushMessageToSnackbar={pushMessageToSnackbar}
            refreshAccountSlots={refreshAccountSlots}
            userData={userData}
            seletedPlanPrice={
              plans.find(plan => {
                if (plan.name === selectedPlan) {
                  return true;
                }
                return false;
              }).price
            }
          />
        );
      }
      default:
        throw new Error("No branch selected in switch statement.");
    }
  };

  handleBack = () => {
    this.setState({ activeStep: "Plan" });
  };

  handleNext = () => {
    this.setState({ activeStep: "Payment method" });
  };

  render() {
    return <Fragment>{this.printContent()}</Fragment>;
  }
}

UpdateSubscription.propTypes = {
  plans: PropTypes.array.isRequired,
  setPaperOpen: PropTypes.func.isRequired,
  userData: PropTypes.object.isRequired,
  fetchTransactions: PropTypes.func.isRequired,
  refreshUserData: PropTypes.func.isRequired,
  pushMessageToSnackbar: PropTypes.func,
  refreshAccountSlots: PropTypes.func.isRequired
};

export default UpdateSubscription;
