import React, { Fragment } from "react";
import PropTypes from "prop-types";
import {
  Typography,
  List,
  ListItem,
  ListItemText,
  Radio,
  ListItemSecondaryAction
} from "@material-ui/core";
import ClickableComponent from "../../../universalComponents/ClickableComponent";

function AddScheduledPostType(props) {
  const { types, uploadType, onSelect } = props;
  return (
    <Fragment>
      <Typography paragraph variant="h6">
        What do you want to upload?
      </Typography>
      <List disablePadding>
        {types.map((element, index) => (
          <ClickableComponent
            key={index}
            function={onSelect}
            parameters={[element.name]}
          >
            <ListItem button divider={index !== types.length - 1}>
              <ListItemText
                primary={element.name}
                secondary={element.description}
              />
              <ListItemSecondaryAction className="p-0">
                <Radio checked={uploadType === element.name} />
              </ListItemSecondaryAction>
            </ListItem>
          </ClickableComponent>
        ))}
      </List>
    </Fragment>
  );
}

AddScheduledPostType.propTypes = {
  types: PropTypes.array,
  uploadType: PropTypes.string,
  onSelect: PropTypes.func
};

export default AddScheduledPostType;
