import React, { Fragment } from "react";
import PropTypes from "prop-types";
import { ListItemText, Button, Toolbar } from "@material-ui/core";
import withWidth, { isWidthUp } from "@material-ui/core/withWidth";
import { withStyles } from "@material-ui/core/styles";
import currencyPrettyPrint from "../../../universalComponents/currencyPrettyPrint";
import ColorfulChip from "../../../universalComponents/ColorfulChip";

const formatDistance = require("date-fns/formatDistance/index.js");

const styles = theme => ({
  balanceChip: {
    height: theme.spacing(3),
    marginLeft: theme.spacing(1)
  }
});

function SubscriptionInfo(props) {
  const { userData, setPaperOpen, width, theme, classes } = props;
  function getSubscriptionStatus() {
    if (!userData) {
      return "";
    }
    if (userData.plan_name && userData.next_billing !== null) {
      return `Subscribed to ${userData.max_accounts} ${
        userData.max_accounts > 1 ? "accounts" : "account"
      }`;
    }
    const now = new Date().getTime() / 1000;
    if (userData.trial_until < now) {
      return "Not subscribed";
    }
    return `${formatDistance(
      new Date(),
      new Date(Number(userData.trial_until) * 1000)
    )} left for trial`;
  }
  return (
    <Fragment>
      <Toolbar className="justify-content-between">
        <ListItemText
          primary={
            <Fragment>
              <span>Status</span>
              {isWidthUp("sm", width) ? null : (
                <span>
                  {userData && userData.balance ? (
                    <ColorfulChip
                      color={theme.palette.primary.main}
                      label={currencyPrettyPrint(userData.balance)}
                      className={classes.balanceChip}
                    />
                  ) : (
                    ""
                  )}
                </span>
              )}
            </Fragment>
          }
          secondary={getSubscriptionStatus()}
        />
        <Button
          variant="contained"
          color="secondary"
          onClick={() => {
            setPaperOpen("UpdateSubscription");
          }}
        >
          Update
        </Button>
      </Toolbar>
    </Fragment>
  );
}

SubscriptionInfo.propTypes = {
  userData: PropTypes.object,
  setPaperOpen: PropTypes.func.isRequired,
  width: PropTypes.string.isRequired,
  theme: PropTypes.object.isRequired,
  classes: PropTypes.object.isRequired
};

export default withWidth()(
  withStyles(styles, { withTheme: true })(SubscriptionInfo)
);
