import React, { Fragment } from "react";
import PropTypes from "prop-types";
import {
  List,
  Radio,
  ListItemText,
  ListItemSecondaryAction,
  ListItem,
  Typography,
  Button
} from "@material-ui/core";
import ActionPaper from "../../../universalComponents/ActionPaper";
import currencyPrettyPrint from "../../../universalComponents/currencyPrettyPrint";

function SelectPlan(props) {
  const { plans, selectPlan, selectedPlan, setActiveStep, userData } = props;
  return (
    <ActionPaper
      maxWidth="sm"
      title="Choose your plan"
      content={
        <List disablePadding>
          {plans.map((plan, index) => {
            const disabled =
              userData.next_billing !== null &&
              plan.name === userData.plan_name;

            return (
              <ListItem
                button
                disabled={disabled}
                divider={index !== plans.length - 1}
                key={plan.name}
                onClick={() => {
                  selectPlan(plan.name);
                }}
              >
                <ListItemText
                  primary={`${plan.name}${
                    plan.name === userData.plan_name &&
                    userData.next_billing !== null
                      ? " - subscribed"
                      : ""
                  }`}
                  secondary={
                    <Typography>
                      {currencyPrettyPrint(plan.price)}
                      <span className="text-secondary-greyed">/mo</span>
                    </Typography>
                  }
                />
                <ListItemSecondaryAction>
                  <Radio
                    checked={selectedPlan === plan.name}
                    disabled={disabled}
                  />
                </ListItemSecondaryAction>
              </ListItem>
            );
          })}
        </List>
      }
      actions={
        <Fragment>
          <Button
            className="mr-1"
            onClick={() => {
              setActiveStep("SelectOption");
            }}
          >
            Back
          </Button>
          <Button
            variant="contained"
            color="secondary"
            disabled={!selectedPlan}
            onClick={() => {
              setActiveStep("ConfirmPlanSwitch");
            }}
          >
            Next
          </Button>
        </Fragment>
      }
    />
  );
}

SelectPlan.propTypes = {
  plans: PropTypes.array.isRequired,
  selectPlan: PropTypes.func.isRequired,
  selectedPlan: PropTypes.string,
  setActiveStep: PropTypes.func.isRequired,
  userData: PropTypes.object
};

export default SelectPlan;
