import React, { Fragment } from "react";
import PropTypes from "prop-types";
import {
  Typography,
  IconButton,
  Tabs,
  Tab,
  Checkbox,
  List,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  Grid
} from "@material-ui/core";
import { withStyles } from "@material-ui/core/styles";
import ListItemCheckbox from "../../../universalComponents/ListItemCheckbox";
import Bordered from "../../../universalComponents/Bordered";
import ImageCroppDialog from "../../../universalComponents/ImageCropprDialog";
import ImagePaginationElement from "../../../universalComponents/ImagePaginationElement";

const styles = theme => ({
  floatButtonWrapper: {
    position: "absolute",
    top: theme.spacing(1),
    right: theme.spacing(1),
    // Have to set this, so the element overlaps the videoReactPlayer
    zIndex: 1000
  },
  floatButtonSVG: {
    height: "1em",
    width: "1em",
    color: theme.palette.action.active
  },
  inputRoot: {
    width: 190,
    "@media (max-width:  400px)": {
      width: 160
    },
    "@media (max-width:  360px)": {
      width: 140
    },
    "@media (max-width:  340px)": {
      width: 120
    }
  },
  uploadIcon: {
    fontSize: 48,
    transition:
      "color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms"
  },
  inputInput: {
    padding: "9px 14.5px"
  },
  tabsIndicator: {
    backgroundColor: theme.palette.secondary.main
  },
  tabRoot: {
    textTransform: "initial",
    minWidth: 72,
    borderStyle: "solid",
    borderTopLeftRadius: theme.shape.borderRadius,
    borderTopRightRadius: theme.shape.borderRadius,
    fontWeight: theme.typography.fontWeightRegular,
    borderTopWidth: 1,
    borderLeftWidth: 1,
    borderRightWidth: 1,
    borderColor: "white",
    "&:hover": {
      color: theme.palette.primary.main,
      opacity: 1
    },
    minHeight: 0
  },
  imgWrapper: { position: "relative" },
  img: {
    width: "100%",
    border: "1px solid rgba(0, 0, 0, 0.23)",
    borderRadius: theme.shape.borderRadius,
    borderTopLeftRadius: 0,
    borderTopRightRadius: 0
  },
  tabSelected: {
    color: `${theme.palette.text.primary} !important`,
    fontWeight: theme.typography.fontWeightMedium,
    borderTopWidth: 1,
    borderLeftWidth: 1,
    borderRightWidth: 1,
    borderColor: "rgba(0, 0, 0, 0.23)"
  },
  uploadText: {
    transition:
      "color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms"
  }
});

function AddScheduledPostOptions(props) {
  const {
    EmojiTextArea,
    Dropzone,
    ImageCroppr,
    classes,
    DateTimePicker,
    tabIndex,
    onEmojiTextareaChange,
    cropprFile,
    onCrop,
    onCropprClose,
    files,
    selectedFileIndex,
    deleteItem,
    onDrop,
    uploadType,
    changeSelectedFile,
    handleTabChange,
    captionValue,
    firstCommentValue,
    captionHashtags,
    captionCharacters,
    firstCommentCharacters,
    firstCommentHashtags,
    handleCheckboxChange,
    uploadAt,
    onChangeUploadAt,
    deleteAt,
    deleteLaterOpen,
    onChangeDeleteAt,
    editScheduledPostId
  } = props;
  function passOnEmojiTextareaChange(value, characters, hashtags) {
    if (tabIndex === 0) {
      onEmojiTextareaChange(value, characters, hashtags, "caption");
    } else {
      onEmojiTextareaChange(value, characters, hashtags, "firstComment");
    }
  }
  function printFile() {
    if (files[selectedFileIndex]) {
      return (
        <div className={classes.imgWrapper}>
          {files[selectedFileIndex].type.includes("image") ? (
            <img
              alt=""
              src={files[selectedFileIndex].preview}
              className={classes.img}
              style={{ height: uploadType === "Story" ? "auto" : 151 }}
            />
          ) : (
            <video
              controls
              className={classes.img}
              style={{ height: uploadType === "Story" ? "auto" : 151 }}
            >
              <source src={files[selectedFileIndex].preview} type="video/mp4" />
            </video>
          )}
          <div className={classes.floatButtonWrapper}>
            <IconButton onClick={deleteItem}>
              <svg
                aria-hidden="true"
                role="img"
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 352 512"
                className={classes.floatButtonSVG}
              >
                <path
                  fill="currentColor"
                  d="M242.72 256l100.07-100.07c12.28-12.28 12.28-32.19 0-44.48l-22.24-22.24c-12.28-12.28-32.19-12.28-44.48 0L176 189.28 75.93 89.21c-12.28-12.28-32.19-12.28-44.48 0L9.21 111.45c-12.28 12.28-12.28 32.19 0 44.48L109.28 256 9.21 356.07c-12.28 12.28-12.28 32.19 0 44.48l22.24 22.24c12.28 12.28 32.2 12.28 44.48 0L176 322.72l100.07 100.07c12.28 12.28 32.2 12.28 44.48 0l22.24-22.24c12.28-12.28 12.28-32.19 0-44.48L242.72 256z"
                />
              </svg>
            </IconButton>
          </div>
        </div>
      );
    }
    return (
      <Dropzone
        accept="image/png, image/jpeg, video/mp4"
        onDrop={onDrop}
        fullHeight
      >
        <span className={classes.uploadText}>
          Click / Drop file <br /> here
        </span>
      </Dropzone>
    );
  }

  function printDropzoneTextarea() {
    if (uploadType === "Story") {
      if (files[selectedFileIndex]) {
        return (
          <div style={{ maxWidth: 250, height: "auto" }}>{printFile()}</div>
        );
      }
      return printFile();
    }
    return (
      <Fragment>
        <Tabs
          value={tabIndex}
          indicatorColor="primary"
          textColor="primary"
          onChange={handleTabChange}
          classes={{
            indicator: "d-none"
          }}
          style={{ minHeight: 0 }}
        >
          <Tab
            label="Caption"
            classes={{
              root: classes.tabRoot,
              selected: classes.tabSelected
            }}
          />
          <Tab
            label="First comment"
            classes={{
              root: classes.tabRoot,
              selected: classes.tabSelected
            }}
          />
        </Tabs>
        {EmojiTextArea && (
          <EmojiTextArea
            noTopBorder
            onChange={passOnEmojiTextareaChange}
            value={tabIndex === 0 ? captionValue : firstCommentValue}
            characters={
              tabIndex === 0 ? captionCharacters : firstCommentCharacters
            }
            hashtags={tabIndex === 0 ? captionHashtags : firstCommentHashtags}
            maxChars={2200}
            maxHashtags={30}
            rightContent={printFile()}
          />
        )}
      </Fragment>
    );
  }
  function printImagePagination() {
    const arr = [];
    for (let i = 0; i < files.length; i += 1) {
      arr.push(
        <Grid item xs={2} sm={1} key={files[i].key}>
          <ImagePaginationElement
            index={i}
            selected={selectedFileIndex === i}
            onSelect={changeSelectedFile}
            src={
              files[i].type.includes("video")
                ? `data:image/png;base64, ${files[i].thumbnailDataUrl}`
                : files[i].preview
            }
          />
        </Grid>
      );
    }
    if (files.length < 10) {
      arr.push(
        <Grid item xs={2} sm={1} key="lastElement">
          <ImagePaginationElement
            index={files.length}
            selected={selectedFileIndex === files.length}
            key="lastElement"
            onSelect={changeSelectedFile}
          />
        </Grid>
      );
    }
    return (
      <Grid container>
        <Grid item lg={8} md={10} xs={12}>
          <Grid container spacing={1}>
            {arr}
          </Grid>
        </Grid>
      </Grid>
    );
  }
  function getTitle() {
    if (editScheduledPostId) {
      return "Edit your scheduled post";
    }
    switch (uploadType) {
      case "Picture/Video":
        return "Schedule a picture/video";
      case "Story":
        return "Schedule a story";
      case "Album":
        return "Schedule an album";
      default:
        throw new Error("No branch selected in switch statement");
    }
  }
  function getAspectRatio() {
    switch (uploadType) {
      case "Story":
        return 16 / 9;
      case "Album":
        return 1;
      default:
        return null;
    }
  }
  return (
    <Fragment>
      {ImageCroppr && (
        <ImageCroppDialog
          // Have to do it this way, otherwise it will set open to null and this is not valid
          open={cropprFile ? true : false}
          ImageCroppr={ImageCroppr}
          src={cropprFile ? cropprFile.preview : ""}
          onCrop={onCrop}
          onClose={onCropprClose}
          // 9 / 16 is fixed aspect ratio for Instagram storys
          aspectRatio={getAspectRatio()}
        />
      )}
      <Typography paragraph variant="h6">
        {getTitle()}
      </Typography>
      <div className="mb-2">{printDropzoneTextarea()}</div>
      {uploadType === "Album" && (
        <div className="mb-2">{printImagePagination()}</div>
      )}
      <Typography paragraph variant="h6" className="mt-3">
        Options
      </Typography>
      {uploadType !== "Story" ? (
        <Fragment>
          <List className="w-100">
            <Bordered disableVerticalPadding>
              <ListItem divider className="listItemSecondaryPadding">
                <ListItemText>
                  <Typography variant="body2">Upload at</Typography>
                </ListItemText>
                <ListItemSecondaryAction>
                  {DateTimePicker && (
                    <DateTimePicker
                      value={uploadAt}
                      onChange={onChangeUploadAt}
                      disablePast
                    />
                  )}
                </ListItemSecondaryAction>
              </ListItem>
              <ListItemCheckbox
                divider
                checkbox={
                  <Checkbox
                    checked={deleteLaterOpen}
                    onChange={handleCheckboxChange("deleteLaterOpen")}
                    color="primary"
                    name="deleteLaterOpen"
                    className="p-0"
                  />
                }
                text="Delete at"
                secondaryAction={
                  DateTimePicker && (
                    <DateTimePicker
                      value={deleteAt}
                      disabled={!deleteLaterOpen}
                      onChange={onChangeDeleteAt}
                      minDate={uploadAt}
                      disablePast
                    />
                  )
                }
                tooltip="Delete the item at the timepoint from Instagram."
              />
            </Bordered>
          </List>
        </Fragment>
      ) : (
        <List className="w-100 mt-2">
          <Bordered disableVerticalPadding>
            <ListItem className="listItemSecondaryPadding">
              <ListItemText className="pl-3">
                <Typography variant="body2">Upload at</Typography>
              </ListItemText>
              <ListItemSecondaryAction className="pr-3">
                {DateTimePicker && (
                  <DateTimePicker
                    onChange={onChangeUploadAt}
                    value={uploadAt}
                    disablePast
                  />
                )}
              </ListItemSecondaryAction>
            </ListItem>
          </Bordered>
        </List>
      )}
    </Fragment>
  );
}

AddScheduledPostOptions.propTypes = {
  onEmojiTextareaChange: PropTypes.func,
  EmojiTextArea: PropTypes.oneOfType([PropTypes.func, PropTypes.element]),
  Dropzone: PropTypes.oneOfType([PropTypes.func, PropTypes.element]),
  ImageCroppr: PropTypes.oneOfType([PropTypes.func, PropTypes.element]),
  classes: PropTypes.object,
  DateTimePicker: PropTypes.oneOfType([PropTypes.func, PropTypes.element]),
  tabIndex: PropTypes.number,
  cropprFile: PropTypes.object,
  onCrop: PropTypes.func,
  onCropprClose: PropTypes.func,
  files: PropTypes.array,
  selectedFileIndex: PropTypes.number,
  deleteItem: PropTypes.func,
  onDrop: PropTypes.func,
  uploadType: PropTypes.string,
  changeSelectedFile: PropTypes.func,
  handleTabChange: PropTypes.func,
  captionValue: PropTypes.string,
  firstCommentValue: PropTypes.string,
  captionHashtags: PropTypes.number,
  captionCharacters: PropTypes.number,
  firstCommentCharacters: PropTypes.number,
  firstCommentHashtags: PropTypes.number,
  handleCheckboxChange: PropTypes.func,
  uploadAt: PropTypes.instanceOf(Date),
  onChangeUploadAt: PropTypes.func,
  deleteAt: PropTypes.instanceOf(Date),
  deleteLaterOpen: PropTypes.bool,
  onChangeDeleteAt: PropTypes.func,
  editScheduledPostId: PropTypes.number
};

export default withStyles(styles, { withTheme: true })(AddScheduledPostOptions);
