import React, { PureComponent, Fragment } from "react";
import PropTypes from "prop-types";
import { Button } from "@material-ui/core";
import ActionPaper from "../../../universalComponents/ActionPaper";
import AddScheduledPostType from "./AddScheduledPostType";
import AddScheduledPostOptions from "./AddScheduledPostOptions";
import UploadProgressDialog from "../../../universalComponents/UploadProgressDialog";
import countWithEmojis from "../../../universalComponents/countWithEmojis";
import countHashtags from "../../../universalComponents/countHashtags";

const now = new Date();

async function filePathToDataUrl(filePath) {
  return new Promise(resolve => {
    const ajax = new XMLHttpRequest();
    ajax.responseType = "blob";
    // force the HTTP response, response-type header to be blob
    ajax.open("GET", filePath);
    ajax.onload = () => {
      const blob = ajax.response;
      const fileReader = new FileReader();
      fileReader.onload = event => {
        resolve(event.target.result);
      };
      fileReader.readAsDataURL(blob);
    };
    ajax.send();
  });
}

const state = {
  activeStep: 0,
  // types: picture/video, story, album
  uploadType: null,
  files: [],
  // This is 0 for uploadType != album and index of album element for album
  selectedFileIndex: 0,
  cropFunction: null,
  captionValue: "",
  firstCommentValue: "",
  captionCharacters: 0,
  firstCommentCharacters: 0,
  captionHashtags: 0,
  firstCommentHashtags: 0,
  deleteLaterOpen: false,
  locationOpen: false,
  location: null,
  uploadAt: now,
  // 3 days
  deleteAt: new Date(now.getTime() + 259200000),
  loading: false,
  progress: 0,
  tabIndex: 0,
  cropprFile: null,
  editScheduledPostId: null
};

function dataURItoBlob(dataURI) {
  return new Promise(resolve => {
    // convert base64 to raw binary data held in a string
    // doesn't handle URLEncoded DataURIs - see SO answer #6850276 for code that does this
    const byteString = atob(dataURI.split(",")[1]);
    // separate out the mime component
    const mimeString = dataURI
      .split(",")[0]
      .split(":")[1]
      .split(";")[0];
    // write the bytes of the string to an ArrayBuffer
    const ab = new ArrayBuffer(byteString.length);
    // create a view into the buffer
    const ia = new Uint8Array(ab);
    // set the bytes of the buffer to the correct values
    for (let i = 0; i < byteString.length; i += 1) {
      ia[i] = byteString.charCodeAt(i);
    }
    // write the ArrayBuffer to a blob, and you're done
    const blob = new Blob([ab], { type: mimeString });
    resolve(blob);
  });
}

/**
 * We cannot call JSON.stringify on an file object, thats why we have to
 * manually convert it to an object, which can be converted to json with
 * JSON.stringify.
 * Taken from : https://stackoverflow.com/questions/24139216/js-input-file-to-json-with-for-example-json-stringify
 */
function getBlob(blobUrl) {
  return new Promise((resolve, reject) => {
    const ajax = new XMLHttpRequest();
    ajax.open("GET", blobUrl, true);
    ajax.responseType = "blob";
    ajax.onload = async () => {
      if (ajax.status === 200) {
        resolve(ajax.response);
      } else {
        reject();
      }
    };
    ajax.send();
  });
}

class AddScheduledPost extends PureComponent {
  state = { ...state };

  types = [
    {
      name: "Picture/Video",
      description: "Upload a picture or video to Instagram"
    },
    { name: "Story", description: "Upload a story to Instagram" },
    { name: "Album", description: "Upload an album to Instagram" }
  ];

  componentDidMount() {
    const { receiveEditFunctionFromChild } = this.props;
    receiveEditFunctionFromChild(this.onEdit);
  }

  onEdit = async element => {
    let firstCommentCharacters;
    let firstCommentHashtags;
    if (element.first_comment) {
      firstCommentHashtags = countHashtags(element.first_comment);
      firstCommentCharacters = countWithEmojis(element.first_comment);
    } else {
      firstCommentHashtags = 0;
      firstCommentCharacters = 0;
    }
    let location;
    if (element.place_name) {
      location = {
        name: element.place_name,
        lat: element.latitude,
        lng: element.longitude
      };
    } else {
      location = null;
    }
    const editScheduledPostId = element.id;
    const firstCommentValue = element.first_comment;
    const activeStep = 1;
    const captionValue = element.caption;
    const captionHashtags = countHashtags(element.caption);
    const captionCharacters = countWithEmojis(element.caption);
    const uploadType = element.type;
    const uploadAt = new Date(element.upload_at_timestamp * 1000);
    let deleteAt;
    let deleteLaterOpen;
    if (element.delete_at_timestamp) {
      deleteAt = new Date(element.delete_at_timestamp * 1000);
      deleteLaterOpen = true;
    } else {
      /**
       * upload + 3 days
       */
      deleteLaterOpen = false;
      deleteAt = new Date(uploadAt.getTime() + 259200000);
    }
    this.setState(
      {
        activeStep,
        uploadType,
        uploadAt,
        deleteAt,
        deleteLaterOpen,
        captionValue,
        firstCommentValue,
        editScheduledPostId,
        firstCommentCharacters,
        firstCommentHashtags,
        captionCharacters,
        captionHashtags,
        location,
        locationOpen: location !== null
      },
      // Has to be done after state change as some state values are critical for called functions
      async () => {
        if (element.type === "Album") {
          let dataUrl;
          let blob;
          for (let i = 0; i < element.items.length; i += 1) {
            /* eslint-disable-next-line */
            dataUrl = await filePathToDataUrl(
              `https://ifamer.com/uploads/${element.items[i].file_name}${element.items[i].data_type}`
            );
            /* eslint-disable-next-line */
            blob = await dataURItoBlob(dataUrl);
            blob.preview = dataUrl;
            blob.lastModifiedDate = new Date();
            blob.name = `${element.items[i].file_name}${element.items[i].data_type}`;
            this.onDrop([blob], [], null, true);
          }
        } else {
          const dataUrl = await filePathToDataUrl(
            `https://ifamer.com/uploads/${element.file_name}${element.data_type}`
          );
          const blob = await dataURItoBlob(dataUrl);
          blob.preview = dataUrl;
          blob.lastModifiedDate = new Date();
          blob.name = `${element.file_name}${element.data_type}`;
          this.onDrop([blob], [], null, true);
        }
      }
    );
  };

  handleRadioChange = type => {
    this.setState({ uploadType: type });
  };

  acceptDrop = file => {
    const { uploadType } = this.state;
    let { files } = this.state;
    if (uploadType === "Album") {
      files = [...files];
      files.push(file);
      this.setState({ files });
    } else {
      this.setState({ files: [file] });
    }
  };

  onDrop = (acceptedFiles, rejectedFiles, ___, skipCrop) => {
    const { uploadType } = this.state;
    const { pushMessageToSnackbar } = this.props;
    if (acceptedFiles.length + rejectedFiles.length > 1) {
      pushMessageToSnackbar({
        isErrorMessage: true,
        text: "You cannot upload more than one file at once"
      });
    } else if (acceptedFiles.length === 0) {
      pushMessageToSnackbar({
        isErrorMessage: true,
        text: "The file you wanted to upload isn't an image or video"
      });
    } else if (acceptedFiles.length === 1) {
      /**
       * Our maximum file size is 160 megabytes, here
       * converted to 167,772,160 bytes
       */
      const file = acceptedFiles[0];
      file.preview = URL.createObjectURL(file);
      /**
       * Unix in milliseconds
       */
      file.key = new Date().getTime();
      if (file.type.includes("video")) {
        const video = document.createElement("video");
        video.addEventListener("loadedmetadata", async () => {
          const durationMilliSeconds = video.duration * 1000;
          video.remove();
          if (uploadType === "Story") {
            if (durationMilliSeconds > 15000) {
              pushMessageToSnackbar({
                isErrorMessage: true,
                text:
                  "The maximum length for a story with a video on Instagram is 15 seconds"
              });
            } else if (durationMilliSeconds < 1000) {
              pushMessageToSnackbar({
                isErrorMessage: true,
                text:
                  "The minimum length for a story with a video on Instagram is one second"
              });
            } else {
              file.thumbnailDataUrl = await this.getThumbnailDataUrl(file);
              this.acceptDrop(file);
            }
          } else if (durationMilliSeconds > 60000) {
            pushMessageToSnackbar({
              isErrorMessage: true,
              text: "The maximum length for a video on Instagram is 60 seconds"
            });
          } else if (durationMilliSeconds < 3000) {
            pushMessageToSnackbar({
              isErrorMessage: true,
              text: "The minimum length for a video on Instagram 3 seconds"
            });
          } else {
            file.thumbnailDataUrl = await this.getThumbnailDataUrl(file);
            this.acceptDrop(file);
          }
        });
        video.src = file.preview;
      } else if (skipCrop) {
        this.acceptDrop(file);
      } else {
        this.setState({ cropprFile: file });
      }
    }
  };

  onEmojiTextareaChange = (value, characters, hashtags, type) => {
    if (type === "caption") {
      this.setState({
        captionValue: value,
        captionCharacters: characters,
        captionHashtags: hashtags
      });
    } else {
      this.setState({
        firstCommentValue: value,
        firstCommentCharacters: characters,
        firstCommentHashtags: hashtags
      });
    }
  };

  selectType = uploadType => {
    this.setState({ uploadType });
  };

  deleteItem = () => {
    let { files } = this.state;
    const { selectedFileIndex } = this.state;
    files = [...files];
    files.splice(selectedFileIndex, 1);
    this.setState({ files });
  };

  addLocation = location => {
    this.setState({ location });
  };

  onChangeUploadAt = uploadAt => {
    const { deleteAt } = this.state;
    const uploadAtUnix = uploadAt.getTime();
    if (uploadAtUnix > deleteAt.getTime() + 60000) {
      this.setState({
        deleteAt: new Date(uploadAt.getTime() + 60000),
        uploadAt
      });
    } else {
      this.setState({ uploadAt });
    }
  };

  onChangeDeleteAt = deleteAt => {
    this.setState({ deleteAt });
  };

  onCropprClose = () => {
    this.setState({ cropprFile: null });
  };

  changeSelectedFile = selectedFileIndex => {
    this.setState({ selectedFileIndex });
  };

  getContent = () => {
    const {
      activeStep,
      uploadType,
      selectedFileIndex,
      files,
      captionValue,
      firstCommentValue,
      captionCharacters,
      firstCommentCharacters,
      captionHashtags,
      firstCommentHashtags,
      deleteLaterOpen,
      locationOpen,
      location,
      uploadAt,
      deleteAt,
      tabIndex,
      cropprFile,
      editScheduledPostId
    } = this.state;
    const {
      Dropzone,
      EmojiTextArea,
      DateTimePicker,
      currentInstaId,
      ImageCroppr
    } = this.props;
    switch (activeStep) {
      case 0:
        return (
          <AddScheduledPostType
            onSelect={this.selectType}
            types={this.types}
            uploadType={uploadType}
          />
        );
      case 1:
        return (
          <AddScheduledPostOptions
            uploadType={uploadType}
            EmojiTextArea={EmojiTextArea}
            Dropzone={Dropzone}
            selectedFileIndex={selectedFileIndex}
            files={files}
            onDrop={this.onDrop}
            deleteItem={this.deleteItem}
            captionValue={captionValue}
            firstCommentValue={firstCommentValue}
            captionCharacters={captionCharacters}
            firstCommentCharacters={firstCommentCharacters}
            captionHashtags={captionHashtags}
            firstCommentHashtags={firstCommentHashtags}
            onEmojiTextareaChange={this.onEmojiTextareaChange}
            DateTimePicker={DateTimePicker}
            handleCheckboxChange={this.handleCheckboxChange}
            deleteLaterOpen={deleteLaterOpen}
            locationOpen={locationOpen}
            currentInstaId={currentInstaId}
            location={location}
            addLocation={this.addLocation}
            uploadAt={uploadAt}
            deleteAt={deleteAt}
            onChangeUploadAt={this.onChangeUploadAt}
            onChangeDeleteAt={this.onChangeDeleteAt}
            handleTabChange={this.handleTabChange}
            tabIndex={tabIndex}
            onCrop={this.onCrop}
            ImageCroppr={ImageCroppr}
            cropprFile={cropprFile}
            onCropprClose={this.onCropprClose}
            changeSelectedFile={this.changeSelectedFile}
            editScheduledPostId={editScheduledPostId}
          />
        );
      default:
        throw new Error("No branch selected in switch statement.");
    }
  };

  onCrop = dataUrl => {
    const { cropprFile } = this.state;
    const file = cropprFile;
    file.preview = dataUrl;
    file.isBlob = true;
    this.acceptDrop(file);
    this.setState({ cropprFile: null });
  };

  getBlobs = async () => {
    const { files } = this.state;
    const promises = [];
    for (let i = 0; i < files.length; i += 1) {
      /**
       * When we used the croppr, it means that the preview is in dataUri format, we have to convert
       * it to an blob object.
       */
      if (files[i].isBlob) {
        promises.push(dataURItoBlob(files[i].preview));
      } else {
        promises.push(getBlob(files[i].preview));
      }
    }
    return Promise.all(promises).then(values => values);
  };

  disableUploadButton = () => {
    const { uploadType, files } = this.state;
    switch (uploadType) {
      case "Album":
        if (files.length >= 2) {
          return false;
        }
        return true;
      default:
        if (files.length === 1) {
          return false;
        }
        return true;
    }
  };

  getThumbnailDataUrl = file =>
    new Promise(resolve => {
      if (file.type.includes("video")) {
        const video = document.createElement("video");
        const canvas = document.createElement("canvas");
        const context = canvas.getContext("2d");
        video.addEventListener("loadeddata", () => {
          context.drawImage(video, 0, 0, canvas.width, canvas.height);
          /**
           * substring 22 will delete the first image/png ...
           * from the dataurl, as we have to strip it,
           * before writing the dataUrl to the file
           */

          resolve(canvas.toDataURL("image/png").substring(22));
        });
        video.src = file.preview;
      } else {
        resolve(null);
      }
    });

  handleUpload = () => {
    const { pushMessageToSnackbar, onClose } = this.props;
    pushMessageToSnackbar({
      text: "Your post has been scheduled"
    });
    onClose();
  };

  handleNext = () => {
    const { activeStep } = this.state;
    this.setState({ activeStep: activeStep + 1 });
  };

  handleBack = () => {
    const { uploadType, editScheduledPostId } = this.state;
    this.setState({ ...state }, () => {
      this.setState({ uploadType, editScheduledPostId });
    });
  };

  handleCheckboxChange = name => event => {
    this.setState({ [name]: event.target.checked });
  };

  handleTabChange = (event, tabIndex) => {
    this.setState({ tabIndex });
  };

  getButtons = () => {
    const { activeStep, uploadType } = this.state;
    const { onClose } = this.props;
    switch (activeStep) {
      case 0:
        return (
          <Fragment>
            <Button onClick={onClose} className="mr-1">
              Close
            </Button>
            <Button
              onClick={this.handleNext}
              className="mr-1"
              variant="contained"
              color="secondary"
              disabled={!uploadType}
            >
              Next
            </Button>
          </Fragment>
        );
      case 1:
        return (
          <Fragment>
            <Button onClick={this.handleBack} className="mr-1">
              Back
            </Button>
            <Button
              onClick={this.handleUpload}
              className="mr-1"
              variant="contained"
              color="secondary"
              disabled={this.disableUploadButton()}
            >
              Upload
            </Button>
          </Fragment>
        );
      default:
        throw new Error("No branch selected in switch statement.");
    }
  };

  render() {
    const { loading, progress } = this.state;
    return (
      <Fragment>
        <UploadProgressDialog open={loading} value={progress} />
        <ActionPaper
          helpPadding
          maxWidth="md"
          content={this.getContent()}
          actions={this.getButtons()}
        />
      </Fragment>
    );
  }
}

AddScheduledPost.propTypes = {
  currentInstaId: PropTypes.number,
  pushMessageToSnackbar: PropTypes.func,
  onClose: PropTypes.func,
  Dropzone: PropTypes.oneOfType([PropTypes.element, PropTypes.func]),
  EmojiTextArea: PropTypes.oneOfType([PropTypes.element, PropTypes.func]),
  DateTimePicker: PropTypes.oneOfType([PropTypes.element, PropTypes.func]),
  ImageCroppr: PropTypes.oneOfType([PropTypes.element, PropTypes.func]),
  receiveEditFunctionFromChild: PropTypes.func
};

export default AddScheduledPost;
