import React, { Fragment } from "react";
import PropTypes from "prop-types";
import { Button } from "@material-ui/core";
import AccountBalanceIcon from "@material-ui/icons/AccountBalance";
import CancelIcon from "@material-ui/icons/Cancel";
import SwapHorizontalCircleIcon from "@material-ui/icons/SwapHorizontalCircle";
import BigButton from "./BigButton";
import ActionPaper from "../../../universalComponents/ActionPaper";

function SelectOption(props) {
  const { onClose, setActiveStep, openAddBalance, userData } = props;
  return (
    <ActionPaper
      maxWidth="sm"
      title="What do you want to do?"
      content={
        <Fragment>
          <BigButton
            noBottomBorderRadius
            noBottomBorder
            primaryText="Add balance"
            icon={<AccountBalanceIcon color="primary" />}
            onClick={openAddBalance}
          />
          <BigButton
            noTopBorderRadius
            noBottomBorderRadius
            primaryText="Change subscription"
            icon={<SwapHorizontalCircleIcon color="primary" />}
            onClick={() => {
              setActiveStep("SwitchPlan");
            }}
          />
          <BigButton
            noTopBorder
            noTopBorderRadius
            primaryText="Cancel subscription"
            icon={<CancelIcon color="primary" />}
            onClick={() => {
              setActiveStep("ConfirmSubscriptionCancelation");
            }}
            disabled={userData.next_billing === null}
          />
        </Fragment>
      }
      actions={
        <Button onClick={onClose} className="mr-1">
          Close
        </Button>
      }
    />
  );
}

SelectOption.propTypes = {
  onClose: PropTypes.func.isRequired,
  setActiveStep: PropTypes.func.isRequired,
  openAddBalance: PropTypes.func.isRequired,
  userData: PropTypes.object.isRequired
};

export default SelectOption;
