import React, { PureComponent, Fragment } from "react";
import PropTypes from "prop-types";
import { Button, Typography, Divider } from "@material-ui/core";
import ActionPaper from "../../../universalComponents/ActionPaper";
import ButtonCircularProgress from "../../../universalComponents/ButtonCircularProgress";
import currencyPrettyPrint from "../../../universalComponents/currencyPrettyPrint";

class ConfirmSubscriptionCancelation extends PureComponent {
  state = { loading: false };

  onConfirm = () => {
    const { onSuccess, pushMessageToSnackbar } = this.props;
    this.setState({ loading: true });
    setTimeout(() => {
      pushMessageToSnackbar({ text: "Your subscription has been canceled" });
      this.setState({ loading: false });
      onSuccess();
    }, 1500);
  };

  render() {
    const { setActiveStep, userData } = this.props;
    const { loading } = this.state;
    return (
      <ActionPaper
        maxWidth="sm"
        title="Do you really want to cancel your subscription?"
        content={
          <Fragment>
            <div className="d-flex justify-content-between mt-3">
              <Typography variant="body1" paragraph>
                Balance
              </Typography>
              <Typography variant="body1" paragraph>
                {currencyPrettyPrint(userData.balance)}
              </Typography>
            </div>
            <Divider className="my-1" />
            <div className="d-flex justify-content-between">
              <Typography variant="body1" paragraph>
                Balance after cancelation
              </Typography>
              <Typography variant="body1" paragraph>
                <b>{currencyPrettyPrint(userData.balance)}</b>
              </Typography>
            </div>
          </Fragment>
        }
        actions={
          <Fragment>
            <Button
              className="mr-1"
              onClick={() => {
                setActiveStep("SelectOption");
              }}
              disabled={loading}
            >
              No
            </Button>
            <Button
              variant="contained"
              color="secondary"
              onClick={this.onConfirm}
              disabled={loading}
            >
              Yes{loading && <ButtonCircularProgress />}
            </Button>
          </Fragment>
        }
      />
    );
  }
}

ConfirmSubscriptionCancelation.propTypes = {
  setActiveStep: PropTypes.func.isRequired,
  onSuccess: PropTypes.func.isRequired,
  pushMessageToSnackbar: PropTypes.func,
  userData: PropTypes.object.isRequired
};

export default ConfirmSubscriptionCancelation;
