import React from "react";
import PropTypes from "prop-types";
import Switch from "react-router-dom/Switch";
import { withStyles } from "@material-ui/core/styles";
import withRouter from "react-router-dom/withRouter";
import Dashboard from "./dashboard/Dashboard";
import SchedulePosts from "./schedule-posts/SchedulePosts";
import Subscription from "./subscription/Subscription";
import PropsRoute from "../../universalComponents/PropsRoute";

const styles = theme => ({
  contentAreaInnerArea: {
    margin: theme.spacing(1),
    width: "auto",
    [theme.breakpoints.up("xs")]: {
      width: "95%",
      marginLeft: "auto",
      marginRight: "auto",
      marginTop: theme.spacing(4),
      marginBottom: theme.spacing(4)
    },
    [theme.breakpoints.up("sm")]: {
      marginTop: theme.spacing(6),
      marginBottom: theme.spacing(6),
      width: "90%",
      marginLeft: "auto",
      marginRight: "auto"
    },
    [theme.breakpoints.up("md")]: {
      marginTop: theme.spacing(6),
      marginBottom: theme.spacing(6),
      width: "82.5%",
      marginLeft: "auto",
      marginRight: "auto"
    },
    [theme.breakpoints.up("lg")]: {
      marginTop: theme.spacing(6),
      marginBottom: theme.spacing(6),
      width: "70%",
      marginLeft: "auto",
      marginRight: "auto"
    }
  }
});

function ContentArea(props) {
  const {
    classes,
    location,
    EmojiTextArea,
    ImageCroppr,
    Dropzone,
    DateTimePicker,
    pushMessageToSnackbar,
    scheduledPosts,
    userData,
    transactions,
    handleNumberChange,
    handleSwitchToggle,
    selectedAccountInformation,
    handleSelectChange,
    toggleAccountActivation,
    CardChart,
    statistics,
    targets,
    isAccountActivated
  } = props;
  return (
    <div className={classes.contentAreaInnerArea}>
      <Switch>
        <PropsRoute
          location={location}
          path="/schedule-posts"
          component={SchedulePosts}
          EmojiTextArea={EmojiTextArea}
          ImageCroppr={ImageCroppr}
          Dropzone={Dropzone}
          DateTimePicker={DateTimePicker}
          pushMessageToSnackbar={pushMessageToSnackbar}
          scheduledPosts={scheduledPosts}
        />
        <PropsRoute
          location={location}
          path="/subscription"
          component={Subscription}
          email={userData && userData.user_login}
          userData={userData}
          transactions={transactions}
          pushMessageToSnackbar={pushMessageToSnackbar}
        />
        <PropsRoute
          location={location}
          isAccountActivated={isAccountActivated}
          path=""
          component={Dashboard}
          handleNumberChange={handleNumberChange}
          handleSwitchToggle={handleSwitchToggle}
          handleSelectChange={handleSelectChange}
          selectedAccountInformation={selectedAccountInformation}
          toggleAccountActivation={toggleAccountActivation}
          pushMessageToSnackbar={pushMessageToSnackbar}
          CardChart={CardChart}
          statistics={statistics}
          targets={targets}
        />
      </Switch>
    </div>
  );
}

ContentArea.propTypes = {
  classes: PropTypes.object,
  location: PropTypes.object,
  EmojiTextArea: PropTypes.oneOfType([PropTypes.func, PropTypes.element]),
  ImageCroppr: PropTypes.oneOfType([PropTypes.func, PropTypes.element]),
  Dropzone: PropTypes.oneOfType([PropTypes.func, PropTypes.element]),
  DateTimePicker: PropTypes.oneOfType([PropTypes.func, PropTypes.element]),
  pushMessageToSnackbar: PropTypes.func,
  scheduledPosts: PropTypes.array,
  userData: PropTypes.object,
  transactions: PropTypes.array,
  handleNumberChange: PropTypes.func,
  handleSwitchToggle: PropTypes.func,
  selectedAccountInformation: PropTypes.object,
  handleSelectChange: PropTypes.func,
  toggleAccountActivation: PropTypes.func,
  CardChart: PropTypes.oneOfType([PropTypes.func, PropTypes.element]),
  statistics: PropTypes.array,
  targets: PropTypes.array.isRequired,
  isAccountActivated: PropTypes.bool.isRequired
};

export default withRouter(withStyles(styles, { withTheme: true })(ContentArea));
