import React from "react";
import classNames from "classnames";
import PropTypes from "prop-types";
import AddIcon from "@material-ui/icons/Add";
import { withStyles } from "@material-ui/core/styles";
import SelfAligningImage from "./SelfAligningImage";

const styles = theme => ({
  wrapper: {
    borderRadius: theme.shape.borderRadius,
    borderWidth: 1,
    borderStyle: "solid",
    height: "100%",
    width: "100%",
    cursor: "pointer",
    transition: `border-color ${theme.transitions.duration.short}ms ${
      theme.transitions.easing.easeInOut
    }`
  },
  notSelected: {
    borderColor: "rgba(0, 0, 0, 0.23)",
    "&:hover": {
      borderColor: theme.palette.primary.main,
      borderWidth: 2
    }
  },
  selected: {
    borderColor: theme.palette.primary.light,
    cursor: "auto"
  },
  iconWrapper: {
    width: "100%",
    paddingTop: "100%",
    position: "relative"
  },
  icon: {
    position: "absolute",
    left: "50%",
    top: "50%",
    transform: "translate(-50%, -50%)"
  }
});

function ImagePaginationElement(props) {
  const { classes, src, selected, onSelect, index } = props;
  function onClick() {
    onSelect(index);
  }
  return (
    <div
      tabIndex={-1}
      role="button"
      className={classNames(
        classes.wrapper,
        selected ? classes.selected : classes.notSelected
      )}
      onClick={selected ? null : onClick}
      onKeyDown={selected ? null : onClick}
    >
      {src ? (
        <SelfAligningImage src={src} noRoundBorder />
      ) : (
        <div className={classes.iconWrapper}>
          <AddIcon className={classes.icon} />
        </div>
      )}
    </div>
  );
}

ImagePaginationElement.propTypes = {
  classes: PropTypes.object,
  src: PropTypes.string,
  selected: PropTypes.bool,
  onSelect: PropTypes.func,
  index: PropTypes.number
};

export default withStyles(styles, { withTheme: true })(ImagePaginationElement);
