import React, { Fragment, PureComponent } from "react";
import PropTypes from "prop-types";
import { Button, Typography, Divider } from "@material-ui/core";
import ActionPaper from "../../../universalComponents/ActionPaper";
import ButtonCircularProgress from "../../../universalComponents/ButtonCircularProgress";
import currencyPrettyPrint from "../../../universalComponents/currencyPrettyPrint";

class ConfirmPlanSwitch extends PureComponent {
  state = { loading: false };

  onConfirm = () => {
    this.setState({ loading: true });
    const { onSuccess, pushMessageToSnackbar } = this.props;
    setTimeout(() => {
      this.setState({ loading: false });
      pushMessageToSnackbar({ text: "Your subscription has been updated" });
      onSuccess();
    }, 1500);
  };

  render() {
    const { setActiveStep, seletedPlanPrice, userData } = this.props;
    const { loading } = this.state;
    return (
      <ActionPaper
        maxWidth="sm"
        title="Do you really want to change your subscription?"
        content={
          <Fragment>
            <div className="d-flex justify-content-between mt-3">
              <Typography variant="body1" paragraph>
                Balance
              </Typography>
              <Typography variant="body1" paragraph>
                {currencyPrettyPrint(userData.balance)}
              </Typography>
            </div>
            <div className="d-flex justify-content-between">
              <Typography variant="body1">Price for plan</Typography>
              <Typography variant="body1">
                {`-${currencyPrettyPrint(seletedPlanPrice)}`}
              </Typography>
            </div>
            <Divider className="my-1" />
            <div className="d-flex justify-content-between">
              <Typography variant="body1" paragraph>
                Balance after subscription
              </Typography>
              <Typography variant="body1" paragraph>
                <b>
                  {currencyPrettyPrint(userData.balance - seletedPlanPrice)}
                </b>
              </Typography>
            </div>
          </Fragment>
        }
        actions={
          <Fragment>
            <Button
              className="mr-1"
              disabled={loading}
              onClick={() => {
                setActiveStep("SwitchPlan");
              }}
            >
              No
            </Button>
            <Button
              variant="contained"
              color="secondary"
              disabled={loading || userData.balance - seletedPlanPrice < 0}
              onClick={this.onConfirm}
            >
              Yes {loading && <ButtonCircularProgress />}
            </Button>
          </Fragment>
        }
      />
    );
  }
}

ConfirmPlanSwitch.propTypes = {
  setActiveStep: PropTypes.func.isRequired,
  onSuccess: PropTypes.func.isRequired,
  pushMessageToSnackbar: PropTypes.func,
  seletedPlanPrice: PropTypes.number.isRequired,
  userData: PropTypes.object.isRequired
};

export default ConfirmPlanSwitch;
