import React, { Fragment } from "react";
import PropTypes from "prop-types";
import Settings1 from "./Settings1";
import Settings2 from "./Settings2";

function SettingsArea(props) {
  const {
    pushMessageToSnackbar,
    hasFetchedInstaAccounts,
    currentInstaId,
    selectedAccountInformation
  } = props;
  return (
    <Fragment>
      <Settings1
        pushMessageToSnackbar={pushMessageToSnackbar}
        hasFetchedInstaAccounts={hasFetchedInstaAccounts}
        currentInstaId={currentInstaId}
        selectedAccountInformation={selectedAccountInformation}
      />
      <Settings2
        pushMessageToSnackbar={pushMessageToSnackbar}
        hasFetchedInstaAccounts={hasFetchedInstaAccounts}
        currentInstaId={currentInstaId}
        selectedAccountInformation={selectedAccountInformation}
      />
    </Fragment>
  );
}

SettingsArea.propTypes = {
  pushMessageToSnackbar: PropTypes.func,
  hasFetchedInstaAccounts: PropTypes.bool,
  currentInstaId: PropTypes.number,
  selectedAccountInformation: PropTypes.object
};

export default SettingsArea;
