import React, { Fragment } from "react";
import PropTypes from "prop-types";
import { Typography } from "@material-ui/core";
import SettingsArea from "./SettingsArea";
import TargetArea from "./TargetArea";
import AccountInformationArea from "./AccountInformationArea";
import StatisticsArea from "./StatisticsArea";

function Dashboard(props) {
  const {
    CardChart,
    statistics,
    currentInstaId,
    selectedAccountInformation,
    hasFetchedInstaAccounts,
    toggleAccountActivation,
    pushMessageToSnackbar,
    targets,
    hasFetchedTargets,
    fetchTargets,
    updateTargetSrc,
    refreshAccountSlots,
    isAccountActivated
  } = props;
  return (
    <Fragment>
      <StatisticsArea CardChart={CardChart} data={statistics} />
      <Typography className="mt-4" variant="subtitle1" gutterBottom>
        Your Account
      </Typography>
      <AccountInformationArea
        isAccountActivated={isAccountActivated}
        currentInstaId={currentInstaId}
        toggleAccountActivation={toggleAccountActivation}
        hasFetchedInstaAccounts={hasFetchedInstaAccounts}
        refreshAccountSlots={refreshAccountSlots}
      />
      <Typography variant="subtitle1" gutterBottom className="mt-4">
        Settings
      </Typography>
      <SettingsArea
        selectedAccountInformation={selectedAccountInformation}
        pushMessageToSnackbar={pushMessageToSnackbar}
        hasFetchedInstaAccounts={hasFetchedInstaAccounts}
        currentInstaId={currentInstaId}
      />
      <TargetArea
        currentInstaId={currentInstaId}
        pushMessageToSnackbar={pushMessageToSnackbar}
        hasFetchedInstaAccounts={hasFetchedInstaAccounts}
        targets={targets}
        hasFetchedTargets={hasFetchedTargets}
        fetchTargets={fetchTargets}
        updateTargetSrc={updateTargetSrc}
      />
    </Fragment>
  );
}

Dashboard.propTypes = {
  CardChart: PropTypes.oneOfType([PropTypes.func, PropTypes.element]),
  statistics: PropTypes.array,
  currentInstaId: PropTypes.number,
  selectedAccountInformation: PropTypes.object,
  hasFetchedInstaAccounts: PropTypes.bool,
  toggleAccountActivation: PropTypes.func,
  pushMessageToSnackbar: PropTypes.func,
  targets: PropTypes.array.isRequired,
  hasFetchedTargets: PropTypes.bool.isRequired,
  fetchTargets: PropTypes.func.isRequired,
  updateTargetSrc: PropTypes.func.isRequired,
  refreshAccountSlots: PropTypes.func.isRequired,
  isAccountActivated: PropTypes.bool.isRequired
};

export default Dashboard;
